.main-header-nav {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        background-color: tan;

        .main-header-ul-container {
            list-style-type: none;
            margin: 0;
            padding: 0;
            overflow: hidden;
            width: 100%;
            display: flex;
            justify-content: space-around;

            li {
                float: left;

                a {
                    display: block;
                    color: navy;
                    text-align: center;
                    padding: 14px 16px;
                    text-decoration: none;

                    &:hover {
                        background-color: salmon;
                    }
                }
            }
        }
    }

